<template>
  <div class="upload-company-cred">
    <label class="label">{{ label }}</label>
    <div class="upload-content">
      <upload-area
        accept=".gif, .jpg, .jpeg, .png"
        :max-size="2*1024*1024"
        :disabled="!$can('system', 'OP0007')"
        class="upload-cred-area"
        @upload="handleUploadFile">
        <upload-style :url="img" />
      </upload-area>
      <i class="iconfont el-icon-delete delete-btn" @click="handleDelete" />

      <!-- <p class="tip">{{ $t('profile.info.upload_company_logo_tip') }}</p> -->
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';
import UploadArea from '@/components/upload';
import UploadStyle from '@/components/upload/styles/UploadStyle1';

import { OSSUploader } from '@/utils/oss/uploader';

export default {
  components: {
    UploadArea,
    UploadStyle,
  },
  props: {
    url: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      default: `${i18n.t('profile.info.logo')}:`,
    },
  },
  data() {
    return {
      img: this.url,
    };
  },
  watch: {
    url(value) {
      this.img = value;
    },
  },
  methods: {
    // 处理上传图片
    async handleUploadFile(file) {
      // 生成 img 地址，并显示在页面上
      const imgUrl = window.URL.createObjectURL(file);
      this.img = imgUrl;

      // 把图片上传到OSS
      try {
        // const path = await uploadBlobToOSS(file, 'BRAND_IMG');
        // 上传到oss
        const { url } = await OSSUploader.send({
          sourceType: 'CRED_IMG',
          body: file,
        });
        // 按规定格式传给父组件
        this.$emit('upload', { field: 'credentials', value: url });
      } catch (err) {
        this.$errHandler(err);
      }
    },
    handleDelete() {
      this.$emit('delete');
    },
  },
};
</script>

